import React from "react";

class Input extends React.Component {
  constructor() {
    super();
    this.state = {
      score: 0
    };
    this.incrementScore = this.incrementScore.bind(this);
  }

  incrementScore() {
    this.setState = prevState => {
      return {
        score: prevState.score + 1
      };
    };
  }

  render() {
    return (
      <div>
        <h1>The input element</h1>
        <form className>
          <label for="sname">Song Name </label>
          <input
            type="text"
            id="sname"
            name="sname"
            placeholder="enter song name"
          ></input>
        </form>

        <button onClick={this.incrementScore}>thumbs up</button>
        <button onClick={this.incrementScore}>thumbs down</button>
        <br />
        {this.state.score}
        <br />
        <button onClick={this.submit}> Save to Database </button>
      </div>
    );
  }
}

export default Input;
